@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  --backgroundcolor: rgb(25, 22, 39);
  --nameTitle: rgb(255, 255, 255);
  --normalWord: rgb(188, 186, 196);
  --hover: #ff501a;
  --border: rgb(82, 82, 82);
  --socialColor: rgb(110, 87, 224);
  --socialHover: rgba(110, 87, 224, 0.8);
}

body {
  font-family: "Roboto", sans-serif;
}

html {
  font-size: 62.5%;
}


.Parallax1
{
  width: 100%;
  height: 90vh;
  background-image:url("https://www.fullphimz.net/static/5fe2d564b3fa6403ffa11d1c/60d5c358e4790580dceb1a90_poster-bok-soo-tro-ve-2018.jpg");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-attachment: fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  object-fit: cover;
}

.Parallax2
{
  width: 100%;
  height: 90vh;
  background-image:url("https://www.fullphimz.net/static/5fe2d564b3fa6403ffa11d1c/60d3efaf30a3170e6f639da0_poster-da-lam-than-tham.jpg");
  background-origin: content-box;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}
.Banner {
  width: 100%;
  height: 35rem;
  
  
  /* background-position: center; */
  margin: 5rem auto;
  display: flex;
  flex-direction: row;
}

.Banner__social {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  list-style: none;
  line-height: 1.5;
  margin: 10rem 0 0 20rem;
  transition: all 0.2s ease-out;
}

.Banner__social li a {
  cursor: pointer;
  color: var(--socialColor);
  font-size: 2rem;
}

.Banner__social li a:hover {
  color: var(--socialHover);

  /* opacity:0.9; */
}

.Banner__info {
  display: flex;
  flex-direction: column;
  margin: 3rem 10rem 4rem;
  line-height: 1.5;
}

.Banner__info h1 {
  color: var(--nameTitle);
  font-size: 4.8rem;
  text-transform: uppercase;
}

.Banner__info h5 {
  color: var(--normalWord);
  font-size: 2rem;
  font-weight: 500;
}

.Banner__info p {
  color: var(--normalWord);
  font-size: 1.5rem;
  font-weight: 500;
  margin-bottom: 2rem;
}

#btn_watch {
  width: 10rem;
  height: 4.5rem;
  color: var(--nameTitle);
  background-color: var(--socialColor);
  border: none;
  outline: none;
  border-radius: 1rem;
  font-size: 1.5rem;
  cursor: pointer;
  transition: all 0.2s linear;
  margin-bottom: 4rem;
  transition: all 0.2s ease;
}

#btn_watch:hover {
  background-color: var(--socialHover);
}

.Banner__info .Banner-scrollDown {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;

  align-items: center;
  font-size: 1.6rem;
  width: 15rem;
  padding: 1rem 1rem;
  cursor: pointer;
}

.Banner__info .Banner-scrollDown:hover {
  animation: moveScroll 1s linear 1;
}

.Banner__info .Banner-scrollDown i {
  color: var(--socialColor);
  font-size: 1.8rem;
  margin-right: 0.5rem;
}
.Banner__info .Banner-scrollDown span {
  color: var(--nameTitle);
  margin-right: 0.5rem;
}

@keyframes moveScroll {
  0% {
    transform: translateY(0%);
  }
  25% {
    transform: translateY(15%);
  }
  50% {
    transform: translateY(30%);
  }
  75% {
    transform: translateY(15%);
  }
  100% {
    transform: translateY(0%);
  }
}
